.education__container {
  width: 100%;
  background-image: linear-gradient(rgba(0,0,0,1),rgba(13, 218, 23, 0.1));
  overflow: hidden
}

.education__card__container{
  display: flex;
  flex-direction: row;
  margin-top: 4vmax;
  margin-left: 8vmax;
  margin-right: 8vmax;
}


@media screen and (max-width: 750px) {
  .education__card__container{
    flex-direction: column;
  }
}


@media screen and (max-width: 490px) {
  .education__card__container{
    margin-left: 5vmax;
    margin-right: 5vmax;
  }
}